.Modal {
  position: fixed;
  z-index: 200;
  border: 1px solid #eee;
  -webkit-box-shadow: 0 2px 2px #ccc;
          box-shadow: 0 2px 2px #ccc;
  background-color: white;
  padding: 10px;
  text-align: center;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  top: 30%;
  left: 25%;
  width: 50%;
  -webkit-transition: all 0.3s ease-out;
  -o-transition: all 0.3s ease-out;
  transition: all 0.3s ease-out;
}
/* CSS ANIMATION */
.ModalOpen {
  -webkit-animation: openModal 0.4s ease-in forwards;
          animation: openModal 0.4s ease-in forwards;

  /* opacity: 1;
  transform: translateY(0); */
}

.ModalClosed {
  -webkit-animation: closeModal 1s ease-out forwards;
          animation: closeModal 1s ease-out forwards;
  /* opacity: 0;
  transform: translateY(-50%); */
}
/* FOR CSS TRANTISTION */
.fade-slide-enter {
}
.fade-slide-enter-active {
  -webkit-animation: openModal 0.4s ease-out forwards;
          animation: openModal 0.4s ease-out forwards;
}
.fade-slide-exit {
}

.fade-slide-exit-active {
  -webkit-animation: closeModal 1s ease-out forwards;
          animation: closeModal 1s ease-out forwards;
}
@-webkit-keyframes openModal {
  0% {
    opacity: 0;
    -webkit-transform: translateY(-100%);
            transform: translateY(-100%);
  }
  50% {
    opacity: 1;
    -webkit-transform: translateY(50%);
            transform: translateY(50%);
  }
  100% {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}
@keyframes openModal {
  0% {
    opacity: 0;
    -webkit-transform: translateY(-100%);
            transform: translateY(-100%);
  }
  50% {
    opacity: 1;
    -webkit-transform: translateY(50%);
            transform: translateY(50%);
  }
  100% {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}

@-webkit-keyframes closeModal {
  0% {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
  50% {
    opacity: 0.8;
    -webkit-transform: translateY(60%);
            transform: translateY(60%);
  }
  100% {
    opacity: 0;
    -webkit-transform: translateY(-100%);
            transform: translateY(-100%);
  }
}

@keyframes closeModal {
  0% {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
  50% {
    opacity: 0.8;
    -webkit-transform: translateY(60%);
            transform: translateY(60%);
  }
  100% {
    opacity: 0;
    -webkit-transform: translateY(-100%);
            transform: translateY(-100%);
  }
}



